@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Langer", cursive;
}

/* Home Styles */
.p-home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 98vh;
  background-color: #222;
  color: #fff;
  text-align: center;
  width: 400px;
  margin: 5px auto;
  border-radius: 20px;
  box-shadow: inset 6px 6px 10px rgba(250, 250, 250, 0.2),
    inset -6px -6px 10px rgba(250, 250, 250, 0.2);
}
.p-home h2 {
  margin: 30px 0;
}
.p-home p {
  margin: 10px 0;
  font-size: 14px;
}
@media (max-width: 600px) {
  .p-home {
    width: 300px;
  }
}

/* Detailes Styles */
.p-detailes {
  text-align: center;
}
.p-detailes .detaile-img {
  width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 auto;
}
.p-detailes .detaile-img img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}
.p-detailes .detaile-title {
  margin: 10px 0 0 0;
  font-size: 24px;
}
.p-detailes .detaile-artist {
  font-size: 18px;
  margin-bottom: 10px;
}

/* Controls Styles */
.p-controls {
  padding: 10px;
  text-align: center;
}
.p-controls button {
  border: none;
  background-color: transparent;
  padding: 5px;
  font-size: 20px;
  opacity: 0.8;
}
.p-controls .play-btn {
  background-color: tomato;
  padding: 10px;
  color: #fff;
  border-radius: 50%;
  text-align: center;
  margin: 0 10px;
  width: 45px;
}
.p-controls .skip-btn {
  background-color: teal;
  color: #fff;
  width: 40px;
  border-radius: 5px;
}
.p-controls button:hover {
  cursor: pointer;
  opacity: 1;
}

